.menuppal.is_active {
    transform: translate3d(0px, 0px, 0px);
}
.menuppal {
    position: fixed;
border-left: 1px solid rgba(65, 65, 65, 0.096);
    transform: translate3d(100%, 0px, 0px);
    transition: transform 0.35s cubic-bezier(0.05, 1.04, 0.72, 0.98) 0s;
    width: 50vw;
    height: 100vh;
    background-color: rgba(233, 233, 233, 0.082);
    backdrop-filter: saturate(180%) blur(10px);
    right: 0;
    z-index: 1001;

    ul {
        margin-top: 3rem;
