        height: auto;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        position: absolute;
        bottom: 40px;
        left: 40px;
        .redes {
            width: 100%;
            height: 35px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            img {
                width: 35px;
                height: 35px;
            }
        }
        .correo {
            width: 100%;
            text-align: center;
