@font-face {
    font-family: "colegas-book";

    src: url("assets/font/GT-Sectra-Book.woff");
}

@font-face {
    font-family: "colegas-bold";

    src: url("assets/font/f37ginger-bold-webfont.woff");
}
@font-face {
    font-family: "colegas-regular";

    src: url("assets/font/f37ginger-regular-webfont.woff");
}

body {
    background-image: url("./assets/fondo-puntos-01.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    margin: 0;
    padding: 0;
    font-family: "colegas-regular";
    overflow-x: hidden;
}
%contenedor {
    width: 100%;
    height: 100vh;
    display: flex;
}
.contenedor {
    @extend %contenedor;
    justify-content: space-between;
    align-items: center;
    .texto-titulo {
        width: 50%;
        padding-left: 10%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        color: #f33d52;
        h3 {
            font-size: 2rem;
            line-height: 0em;
            margin-bottom: -5px;
        }
        h1 {
            line-height: 0.2em;
            font-size: 5rem;
            margin-bottom: 30px;
            font-family: "Lexend Deca", sans-serif;
        }
        p {
            width: 700px;
            color: black;
            line-height: 1.5rem;
            font-family: "Poppins", sans-serif;
        }
    }
    img {
        width: 40%;
        height: auto;
    }
}
nav {
    position: fixed;
    width: 100vw;
    height: 50px;
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgba(241, 241, 241, 0.171);
    backdrop-filter: saturate(180%) blur(10px);
    justify-content: space-between;
    border-bottom: 1px solid rgba(65, 65, 65, 0.096);
    align-items: center;
    z-index: 100;
    img {
        width: 150px;
        z-index: 2;
        margin-left: 20px;
    }
}
.cuadro {
    width: 50px;
    height: 15px;
    background-color: #f33d52;
}
#AnalisisEstrategia {
    flex-direction: column !important;
    justify-content: space-evenly !important;
    height: 80vh !important;
    .titulo2 {
        width: 40%;
        text-align: center;
        h1 {
            color: #f33d52;
            font-size: 3rem;
            text-align: center;
            line-height: 2rem;
            font-family: "Lexend Deca", sans-serif;
        }
        p {
            margin-top: 0px;
            font-family: "Poppins", sans-serif;
        }
    }
    .cont-num {
        display: flex;
        width: 80%;
        justify-content: space-evenly;
        .content-numeros {
            width: 250px;
            height: auto;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            img {
                width: 110px;
                height: auto;
            }
            h2 {
                color: #f33d52;
                font-family: "Lexend Deca", sans-serif;
            }
            p {
                font-family: "Poppins", sans-serif;
            }
            &:hover {
            }
        }
    }
}

#Recent_Work {
    position: relative;
    margin-top: 100px;
    overflow: hidden;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .cuadro1 {
        position: absolute;
        width: 100vw;
        display: flex;
        justify-content: space-around;
        align-items: center;
        z-index: 6;
        height: 500px;
        background-color: #f33d52;
        -ms-transform: skewY(10deg); /* IE 9 */
        transform: skewY(10deg);
        .imag-cont {
            width: 350px;
            height: 350px;
            -ms-transform: skewY(-10deg); /* IE 9 */
            transform: skewY(-10deg);
            .celular {
                width: 350px;
                height: 350px;
            }

        }
    }
    .cuadro2 {
        z-index: 4;
        position: absolute;
        width: 100vw;
        height: 500px;
        background-color: #d8d8d8;
        -ms-transform: skewY(-10deg); /* IE 9 */
        transform: skewY(-10deg);
    }
    .bote {
        z-index: 5;
        position: absolute;
        top: -90px;
        right: 100px;
        width: 550px;
        height: auto;
    }
    h1 {
        color: white;
        line-height: 0.9em;
        font-family: "Lexend Deca", sans-serif;
        z-index: 20;
        font-size: 5rem;
    }
    a {
        z-index: 20;
        text-decoration: none;
        width: 200px;
        height: 40px;
        border: white solid 3px;
        border-radius: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: "Lexend Deca", sans-serif;
        color: white;
        background-color: #f33d52;
        font-size: 20px;
        transition: 0.3s ease-out;
        &:hover {
            transform: translateY(-5px);
            box-shadow: rgba(0, 0, 0, 0.171) 5px 10px 0px;
        }
    }
}
#Servicios{
    flex-direction: column;
    height: auto !important;
    align-items: center;
    justify-content: center;
    h1{
           color: #f33d52;
        line-height: 0.9em;
        font-family: "Lexend Deca", sans-serif;
        z-index: 20;
        font-size: 3rem;
    }
    .content-btn{
        width: 100%;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
#form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.input-wrapper{
    display: flex;
    flex-direction: column;
    input{
        border: 1px solid rgba(128, 128, 128, 0.205);
        height: 25px;
    }
    label{
        margin-top: 10px;
    }
}
